const configureApiRoot = () => {
    const hostname = window && window.location && window.location.hostname;
    switch (hostname) {
        case 'gc.connectivitysoftwaresolutions.com':
            return 'http://gc.connectivitysoftwaresolutions.com';
        case 'localhost':
            return 'http://localhost:5000';
        default:
            return 'http://gc.connectivitysoftwaresolutions.com';
    }
  };
  
export const API_URL = configureApiRoot();
export const AUTH_URL = configureApiRoot();
export const API_CONSUMER = configureApiRoot();
export const FTP_URL = 'http://gcadmin.connectivitysoftwaresolutions.com'; // Config[KEY].API_URL;
console.log('##*API*##',API_CONSUMER);


export const APP_TOKEN = {
    set: ({userName, name, token}) => {
        sessionStorage.setItem('token', token);
        sessionStorage.setItem('userName', userName);
        sessionStorage.setItem('name', name);
    },
    get: () => ({
        token: sessionStorage.getItem('token'),
        userName : sessionStorage.getItem('userName'),
        name : sessionStorage.getItem('name'),
    }),
    remove: () => {
        sessionStorage.clear();
    },
    get notEmpty() {
        const cond1 = this.get().token !== null;
        const cond2 = this.get().token !== '';
        return cond1 && cond2;
    }
}